export default {
	onLoad (options) {
		
		this.orderIds = JSON.parse(options.orderIds)
		this.initInvoiceInfo()
	},
	data(){
		return{
			orderIds: [],
			invioceInfo: []
		}
	},
	methods:{
		async initInvoiceInfo() {
			let res = await this.$fetch(this.$api.orderCustomer,{ids:this.orderIds},"POST")
			
			if (res.code == 0) {
				this.invioceInfo = res.rows
			}
		}
	}
}
